



































































import { Component, Vue, Prop, Emit, Watch } from "vue-property-decorator";
import { apiUrl } from "../../../api/";

@Component({
  name: "editUser"
})
export default class EditUser extends Vue {
  @Prop({
    type: Object,
    required: true
  })
  userData: any;
  @Prop({
    type: Boolean,
    default: false,
    required: true
  })
  markFlag: any;

  @Emit("closeFun")
  closeFun() {
    (this.$refs["ruleForm"] as any).resetFields();
    return false;
  }
  private options = [
    {
      value: "admin",
      label: "管理员"
    },
    {
      value: "user",
      label: "普通用户"
    }
  ];
  private validatePass = (rule: any, value: string, callback: any) => {
    if (value === "") {
      callback(new Error("请输入密码"));
    } else {
      if (this.userData.checkPass !== "") {
        (this.$refs.ruleForm as any).validateField("checkPass");
      }
      callback();
    }
  };
  private validatePass2 = (rule: any, value: string, callback: any) => {
    if (value === "") {
      callback(new Error("请再次输入密码"));
    } else if (value !== this.userData.password) {
      callback(new Error("两次输入密码不一致!"));
    } else {
      callback();
    }
  };

  private rules = {
    username: [
      { required: true, message: "请输入用户名", trigger: "blur" },
      {
        min: 3,
        max: 20,
        message: "长度在 3 到 20 个字符",
        trigger: "blur"
      }
    ],
    password: [{ validator: this.validatePass, trigger: "blur" }],
    checkPass: [{ validator: this.validatePass2, trigger: "blur" }]
  };
  get objName(): string {
    return JSON.parse(JSON.stringify(this.userData));
  }
  private submitForm(formName: string) {
    (this.$refs[formName] as any).validate((valid: boolean) => {
      if (valid) {
        (this as any).$axios
          .post(apiUrl.userChange, {
            username: this.userData.username,
            id: this.userData.id,
            type: this.userData.type
          })
          .then((res: any) => {
            if (res.data.code == 0) {
              this.$message.success("修改成功");
              this.closeFun();
              (this.$parent as any).getUserData();
            }
          });
        if (this.userData.password && this.userData.checkPass) {
          (this as any).$axios
            .post(apiUrl.userChange, this.userData)
            .then((res: any) => {
              if (res.data.code == 0) {
                this.$message.success("修改成功");
                this.closeFun();
                (this.$parent as any).getUserData();
              }
            });
        }
      } else {
        console.log("error submit!!");
        return false;
      }
    });
  }
}
