


































































































import { Component, Vue, Mixins } from "vue-property-decorator";
import { Getter } from "vuex-class";
import { apiUrl } from "../../api";
import { EditAuth } from "../../mixins";
@Component
export default class TagsList extends EditAuth {
    private tagname = "";
    private tableData: Array<any> = [];
    private page = 1;
    private pageSize = 15;
    private count = 0;
    private dialogVisible = false;
    private multipleSelection = [];
    private ruleForm = {
        id: ""
    };
    private upTags = "";
    private rules = {
        tag_name: [
            { required: true, message: "请输入标签名称", trigger: "blur" },
            {
                min: 1,
                max: 20,
                message: "长度在 1 到 20 个字符",
                trigger: "blur"
            }
        ]
    };
    private diaFlag = false;
    created() {
        this.getTagList();
    }
    private searchHandle() {
        if (this.tagname !== "") {
            (this as any).$axios
                .get(
                    `${apiUrl.tagSearch}?tag_name=${this.tagname}&page=${this.page}&pageSize=${this.pageSize}`
                )
                .then((res: any) => {
                    if (res.data.code === 0) {
                        this.count = res.data.message.count;
                        this.tableData = res.data.message.rows;
                    }
                    console.log(res);
                });
        }
    }
    private clearHandle() {
        this.tagname = "";
        this.getTagList();
    }
    private handleEdit(index: number, row: any) {
        this.dialogVisible = true;
        this.ruleForm = { ...row };
        console.log(index, row);
    }
    private handleDelete(index: number, row: any) {
        this.$confirm("是否删除此条数据？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
        }).then(() => {
            this.deleteItem(row.id);
            this.getTagList();
        });
    }
    private deleteItem(id: number) {
        (this as any).$axios
            .get(`${apiUrl.tagDelete}?id=${id}`)
            .then((res: any) => {
                if (res.data.code == 0) {
                    if (this.diaFlag) {
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                        this.diaFlag = false;
                    }
                }
            });
    }
    private handleClose() {
        this.dialogVisible = false;
        (this.$refs["ruleForm"] as any).resetFields();
        (this as any).ruleForm = {};
    }
    private getTagList() {
        (this as any).$axios
            .get(`${apiUrl.tag}?page=${this.page}&pageSize=${this.pageSize}`)
            .then((res: any) => {
                console.log(res);
                if (res.data.code === 0) {
                    this.count = res.data.message.count;
                    this.tableData = res.data.message.rows;
                }
                console.log(res);
            });
    }
    private submitForm(formName: any) {
        (this.$refs[formName] as any).validate((valid: boolean) => {
            if (valid) {
                (this as any).$axios
                    .post(apiUrl.tagUpdate, this.ruleForm)
                    .then((res: any) => {
                        if (res.data.code === 0) {
                            this.dialogVisible = false;
                            this.$message.success("修改成功");
                            this.getTagList();
                            (this as any).ruleForm = {};
                        }
                    });
            } else {
                console.log("error submit!!");
                return false;
            }
        });
    }
    private handleCurrentChange(index: number) {
        console.log(index);
        this.page = index;
        this.getTagList();
    }
    private handleSelectionChange(val: any) {
        this.multipleSelection = val;
    }
    private allRemove() {
        if (this.multipleSelection.length <= 0) {
            this.$message.warning("请选择信息");
            return false;
        }
        this.multipleSelection.forEach((item: any, index: number) => {
            if (index >= this.multipleSelection.length - 1) {
                this.diaFlag = true;
            }
            this.deleteItem(item.id);
        });
        this.getTagList();
    }
}
