

















































































import { Component, Vue } from "vue-property-decorator";

import { apiUrl } from "../../api";
import { EditAuth } from "../../mixins";
import EditUser from "./components/editUser.vue";
@Component({
    name: "userList",
    components: {
        EditUser
    }
})
export default class UserList extends EditAuth {
    private username: string = "";
    private typeValue: string = "";
    private count: number = 0;
    private page: number = 1;
    private pageSize: number = 16;
    private markFlag: boolean = false;
    private userData = {};
    private multipleSelection = [];
    private diaFlag = false;
    created() {
        this.getUserData();
    }
    private typeArr: Array<any> = [
        {
            value: "admin",
            label: "管理员"
        },
        {
            value: "user",
            label: "普通用户"
        }
    ];
    private tableData: Array<any> = [];
    getUserData(): void {
        (this as any).$axios
            .get(`${apiUrl.user}?page=${this.page}&pageSize=${this.pageSize}`)
            .then((res: any) => {
                if (res.data.code == 0) {
                    this.tableData = res.data.massage.rows;
                    this.count = res.data.massage.count;
                }
            });
    }
    handleEdit(index: number, row: any) {
        this.markFlag = true;
        this.userData = { ...row };
    }
    handleDelete(index: number, row: any) {
        this.$confirm("是否删除此条数据？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
        }).then(() => {
            this.deleteItem(row.id);
            this.getUserData();
        });
    }
    deleteItem(id: number) {
        (this as any).$axios
            .get(`${apiUrl.userDelete}?id=${id}`)
            .then((res: any) => {
                if (res.data.code === 0) {
                    if (this.diaFlag) {
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                        this.diaFlag = false;
                    }
                }
            });
    }
    handleCurrentChange(index: number): void {
        this.page = index;
        this.getUserData();
    }
    //搜索
    searchHandle() {
        if (this.username == "" && this.typeValue == "") {
            return false;
        }
        (this as any).$axios
            .get(
                `${apiUrl.userSearch}?username=${this.username}&type=${this.typeValue}`
            )
            .then((res: any) => {
                console.log(res);
                if (res.data.code == 0) {
                    this.tableData = res.data.massage.rows;
                    this.count = res.data.massage.count;
                }
            });
    }
    clearHandle() {
        this.username = "";
        this.typeValue = "";
        this.getUserData();
    }
    //关闭弹窗
    closeMark(flag: boolean) {
        this.markFlag = flag;
    }
    handleSelectionChange(val: any) {
        this.multipleSelection = val;
    }
    allRemove() {
        if (this.multipleSelection.length <= 0) {
            this.$message.warning("请选择信息");
            return false;
        }
        this.multipleSelection.forEach((item: any, index: number) => {
            if (index >= this.multipleSelection.length - 1) {
                this.diaFlag = true;
            }
            this.deleteItem(item.id);
        });
        this.getUserData();
    }
}
